/* About Me 섹션 스타일 */
.about_section {
  position: relative;
  min-height: 100vh;
  background: #ffffff;
  overflow-x: hidden;
  padding-top: 120px;
  transition: background-color 0.3s ease;
}

[data-theme="dark"] .about_section {
  background: #1a1a1a;
}

.about_container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 24px;
  position: relative;
  z-index: 10;
  width: 100%;
  box-sizing: border-box;
}

.about_title {
  font-size: 40px;
  font-weight: 700;
  color: #000000;
  margin-bottom: 60px;
  text-align: left;
  transition: color 0.3s ease;
}

[data-theme="dark"] .about_title {
  color: #ffffff;
}

.about_content {
  display: flex;
  align-items: flex-start;
  gap: 5%;
  margin-bottom: 100px;
  width: 100%;
  box-sizing: border-box;
}

.about_image {
  flex-shrink: 0;
  width: 250px;
  height: 250px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  max-width: 100%;
}

.profile_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about_text {
  flex: 1;
  min-width: 0;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
  word-break: keep-all;
}

.name {
  font-family: "Fredoka", "Comic Sans MS", cursive;
  font-size: 51px;
  font-weight: 400;
  color: #2c3e50;
  margin-bottom: 30px;
  letter-spacing: 0px;
  text-shadow: 1px 1px 0px #34495e, 2px 2px 4px rgba(44, 62, 80, 0.15);
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

[data-theme="dark"] .name {
  color: #ffffff;
  text-shadow: 1px 1px 0px #e0e0e0, 2px 2px 4px rgba(255, 255, 255, 0.1);
}

.greeting {
  font-size: 21px;
  color: #333333;
  line-height: 1.6;
  margin-bottom: 25px;
  font-weight: 500;
  transition: color 0.3s ease;
}

[data-theme="dark"] .greeting {
  color: #e0e0e0;
}

.description {
  font-size: 18px;
  color: #555555;
  line-height: 1.8;
  margin-bottom: 20px;
  font-weight: 400;
  transition: color 0.3s ease;
}

[data-theme="dark"] .description {
  color: #b0b0b0;
}

.view_more_btn {
  display: flex;
  align-items: center;
  gap: 10px;
  background: none;
  border: none;
  color: #007bff;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  padding: 10px 0;
  transition: all 0.3s ease;
}

.view_more_btn:hover {
  color: #0056b3;
  transform: translateX(5px);
}

.btn_icon {
  width: 20px;
  height: 20px;
  background: #6f42c1;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: all 0.3s ease;
}

.view_more_btn:hover .btn_icon {
  background: #5a32a3;
}

.about_info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  margin-bottom: 80px;
  align-items: start;
  width: 100%;
  box-sizing: border-box;
}

.info_column {
  text-align: left;
}

.info_title {
  font-size: 24px;
  font-weight: 700;
  color: #000000;
  margin-bottom: 25px;
  transition: color 0.3s ease;
}

[data-theme="dark"] .info_title {
  color: #ffffff;
}

.info_list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.info_list li {
  font-size: 18px;
  color: #555555;
  line-height: 1.8;
  margin-bottom: 15px;
  padding: 12px 16px;
  background: #fff9f9;
  border-radius: 8px;
  border-left: 4px solid #f9838f;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 12px;
}

.info_list li:hover {
  background: #fdf2f2;
  transform: translateX(5px);
}

[data-theme="dark"] .info_list li {
  color: #d0d0d0;
  background: rgba(255, 255, 255, 0.05);
  border-left: 4px solid #667eea;
}

[data-theme="dark"] .info_list li:hover {
  background: rgba(255, 255, 255, 0.1);
}

.info_icon {
  font-size: 19px;
  width: 24px;
  text-align: center;
}

.highlight_tech {
  color: #667eea;
  font-weight: 600;
  background: linear-gradient(
    120deg,
    rgba(102, 126, 234, 0.1) 0%,
    rgba(102, 126, 234, 0.2) 100%
  );
  padding: 2px 6px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

[data-theme="dark"] .highlight_tech {
  color: #8b9cff;
  background: linear-gradient(
    120deg,
    rgba(139, 156, 255, 0.2) 0%,
    rgba(139, 156, 255, 0.3) 100%
  );
}

/* 무한 스크롤 애니메이션 */
.infinite_scroll_container {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
  margin: 70px 0;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

.infinite_scroll_text {
  display: flex;
  white-space: nowrap;
  animation: scroll_left 10s linear infinite;
  font-size: 200px;
  font-weight: 900;
  color: transparent;
  -webkit-text-stroke: 2px #667eea;
  opacity: 0.25;
  text-transform: uppercase;
  transition: all 0.3s ease;
  width: 100%;
  box-sizing: border-box;
}

[data-theme="dark"] .infinite_scroll_text {
  -webkit-text-stroke: 2px #8b9cff;
  opacity: 0.15;
}

.infinite_scroll_text span {
  margin-right: 100px;
  flex-shrink: 0;
}

@keyframes scroll_left {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

/* 반응형 디자인 */
@media (max-width: 1024px) {
  .about_content {
    flex-direction: column;
    gap: 40px;
  }
  .profile_img_container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .profile_img{
    width: 75%;
    height: 75%;
  }

  .about_image {
    width: 200px;
    height: 200px;
  }

  .name {
    font-size: 40px;
  }

  .about_container {
    padding: 0 50px;
  }

  .infinite_scroll_container {
    height: 200px;
    margin: 50px 0;
  }

  .infinite_scroll_text {
    font-size: 150px;
    -webkit-text-stroke: 2px #667eea;
    animation: scroll_left 15s linear infinite;
  }

  .infinite_scroll_text span {
    margin-right: 80px;
  }
}

@media (max-width: 768px) {
  .about_section {
    padding-top: 80px;
  }

  .about_container {
    padding: 0 24px;
  }

  .about_title {
    font-size: 29px;
    margin-bottom: 30px;
    text-align: center;
  }

  .about_content {
    flex-direction: column;
    gap: 25px;
    margin-bottom: 40px;
    align-items: center;
  }

  .about_text {
    width: 100%;
    max-width: 100%;
    padding: 0;
    text-align: center;
  }

  .about_image {
    width: 160px;
    height: 160px;
    margin: 0 auto;
  }

  .name {
    font-size: 29px;
    text-align: center;
    margin-bottom: 15px;
  }

  .greeting {
    font-size: 18px;
    margin-bottom: 20px;
    line-height: 1.5;
  }

  .description {
    font-size: 16px;
    margin-bottom: 15px;
    line-height: 1.6;
  }

  .about_info {
    grid-template-columns: 1fr;
    gap: 30px;
    margin-bottom: 40px;
  }

  .info_title {
    font-size: 21px;
    margin-bottom: 20px;
    text-align: center;
  }

  .info_list li {
    font-size: 16px;
    padding: 10px 12px;
    margin-bottom: 10px;
  }

  .infinite_scroll_container {
    height: 80px;
    margin: 30px 0;
  }

  .infinite_scroll_text {
    font-size: 72px;
    -webkit-text-stroke: 1px #667eea;
    animation: scroll_left 12s linear infinite;
  }
}

