  }

  .about_container {
    padding: 0 24px;
  }

  .about_title {
    font-size: 24px;
    margin-bottom: 25px;
    text-align: center;
  }

  .about_content {
    gap: 20px;
    margin-bottom: 30px;
  }

  .about_text {
    width: 100%;
    max-width: 100%;
    padding: 0;
    text-align: center;
  }

  .about_image {
    width: 140px;
    height: 140px;
  }

  .name {
    font-size: 24px;
    margin-bottom: 12px;
  }

  .greeting {
    font-size: 16px;
    margin-bottom: 15px;
    line-height: 1.4;
  }

  .description {
    font-size: 14px;
    margin-bottom: 12px;
    line-height: 1.5;
  }

  .about_info {
    gap: 25px;
    margin-bottom: 30px;
  }

  .info_title {
    font-size: 19px;
    margin-bottom: 15px;
    text-align: center;
  }

  .info_list li {
    font-size: 14px;
    padding: 8px 10px;
    margin-bottom: 8px;
  }

  .infinite_scroll_container {
    height: 70px;
    margin: 25px 0;
  }

  .infinite_scroll_text {
    font-size: 56px;
    -webkit-text-stroke: 1px #667eea;
    animation: scroll_left 10s linear infinite;
  }

  .infinite_scroll_text span {
    margin-right: 40px;
  }
}

@media (max-width: 360px) {
  .about_container {
    padding: 0 24px;
  }

  .about_text {
    width: 100%;
    max-width: 100%;
    padding: 0;
    text-align: center;
  }

  .about_title {
    font-size: 21px;
    margin-bottom: 20px;
