    text-align: center;
  }

  .name {
    font-size: 19px;
  }

  .greeting {
    font-size: 14px;
  }

  .description {
    font-size: 12px;
  }

  .about_image {
    width: 100px;
    height: 100px;
  }

  .infinite_scroll_container {
    height: 50px;
    margin: 15px 0;
  }

  .infinite_scroll_text {
    font-size: 40px;
