/* 전문 프로젝트 섹션 스타일 */
.professional_projects_section {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(135deg, #f8f9ff 0%, #e8f4fd 50%, #f0e8ff 100%);
  position: relative;
  overflow: hidden;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(-50vw + 50%);
  padding: 120px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}

[data-theme="dark"] .professional_projects_section {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 50%, #1e1e1e 100%);
}

.professional_projects_container {
  max-width: 1400px;
  width: 100%;
  padding: 0 40px;
  position: relative;
  z-index: 2;
}

.professional_projects_title {
  font-size: 48px;
  font-weight: 700;
  color: #1e293b;
  text-align: center;
  margin-bottom: 80px;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  transition: color 0.3s ease;
}

[data-theme="dark"] .professional_projects_title {
  color: #ffffff;
}

.professional_projects_grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  align-items: stretch;
}

.professional_project_card {
  background: #ffffff;
  border-radius: 16px;
  padding: 0;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  overflow: hidden;
  border: 1px solid #e2e8f0;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 600px;
}

[data-theme="dark"] .professional_project_card {
  background: #2d2d2d;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.professional_project_card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.professional_project_image_container {
  width: 100%;
  height: 280px;
  background: #f1f5f9;
  border-bottom: 1px solid #e2e8f0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  transition: background 0.3s ease, border-color 0.3s ease;
}

[data-theme="dark"] .professional_project_image_container {
  background: #1a1a1a;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.professional_project_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.professional_project_card:hover .professional_project_image {
  transform: scale(1.05);
}

.professional_project_content {
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex-grow: 1;
}

.professional_project_name {
  font-size: 24px;
  font-weight: 700;
  color: #1e293b;
  margin: 0;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  transition: color 0.3s ease;
}

[data-theme="dark"] .professional_project_name {
  color: #ffffff;
}

.professional_project_period {
  font-size: 16px;
  font-weight: 600;
  color: #64748b;
  margin: 0;
  transition: color 0.3s ease;
}

.professional_project_role {
  font-size: 16px;
  font-weight: 500;
  color: #475569;
  margin: 0;
  transition: color 0.3s ease;
}

[data-theme="dark"] .professional_project_period {
  color: #b0b0b0;
}

[data-theme="dark"] .professional_project_role {
  color: #d0d0d0;
}

.professional_project_tech {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.professional_project_tech_label {
  font-size: 14px;
  font-weight: 600;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: color 0.3s ease;
}

[data-theme="dark"] .professional_project_tech_label {
  color: #b0b0b0;
}

.professional_project_tech_stack {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.professional_project_tech_pill {
  background: #f1f5f9;
  color: #475569;
  padding: 8px 12px;
  border-radius: 16px;
  font-size: 12px;
  font-weight: 500;
  border: 1px solid #e2e8f0;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 6px;
}

[data-theme="dark"] .professional_project_tech_pill {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.professional_project_tech_pill:hover {
  background: #e2e8f0;
  transform: translateY(-1px);
}

.professional_project_tech_icon {
  width: 16px;
  height: 16px;
  object-fit: contain;
  border-radius: 2px;
}

.professional_project_tech_name {
  font-size: 12px;
  font-weight: 500;
  color: #475569;
  transition: color 0.3s ease;
}

[data-theme="dark"] .professional_project_tech_name {
  color: #ffffff;
}

[data-theme="dark"] .professional_project_tech_name:hover {
  color: #475569;
}

.professional_project_description {
  font-size: 16px;
  line-height: 1.6;
  color: #64748b;
  margin: 0;
  flex-grow: 1;
  transition: color 0.3s ease;
}

[data-theme="dark"] .professional_project_description {
  color: #b0b0b0;
}

.professional_project_buttons {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin-top: auto;
}

.professional_project_button {
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.professional_project_button_view {
  background: #3b82f6;
  color: #ffffff;
  border-color: #3b82f6;
}

.professional_project_button_view:hover {
  background: #2563eb;
  border-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.professional_project_button_github {
  background: #1f2937;
  color: #ffffff;
  border-color: #1f2937;
}

.professional_project_button_github:hover {
  background: #111827;
  border-color: #111827;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(31, 41, 55, 0.3);
}

.professional_project_button_demo {
  background: #ffffff;
  color: #3b82f6;
  border-color: #3b82f6;
}

.professional_project_button_demo:hover {
  background: #3b82f6;
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.professional_project_button_guide {
  background: #ffffff;
  color: #10b981;
  border-color: #10b981;
}

.professional_project_button_guide:hover {
  background: #10b981;
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

/* 반응형 디자인 */
@media screen and (max-width: 1200px) {
  .professional_projects_grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    align-items: stretch;
  }

  .professional_projects_title {
    font-size: 40px;
    margin-bottom: 60px;
  }
}

@media screen and (max-width: 1024px) {
  .professional_projects_section {
    padding: 80px 0;
  }

  .professional_projects_container {
    padding: 0 40px;
  }

  .professional_projects_grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 25px;
    align-items: stretch;
  }

  .professional_projects_title {
    font-size: 36px;
    margin-bottom: 50px;
  }

  .professional_project_content {
    padding: 25px;
    gap: 15px;
  }

  .professional_project_name {
    font-size: 20px;
  }

  .professional_project_period {
    font-size: 15px;
  }

  .professional_project_role {
    font-size: 15px;
  }

  .professional_project_description {
    font-size: 15px;
    line-height: 1.6;
  }

  .professional_project_tech_label {
    font-size: 13px;
  }

  .professional_project_tech_pill {
    padding: 7px 12px;
    font-size: 12px;
  }

  .professional_project_image_container {
    height: 200px;
  }

  .professional_project_card {
    min-height: 450px;
  }

  .professional_project_buttons {
    gap: 12px;
  }

  .professional_project_button {
    padding: 14px 20px;
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  .professional_projects_section {
    padding: 80px 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .professional_projects_container {
    padding: 0 24px;
  }

  .professional_projects_grid {
    grid-template-columns: 1fr;
    gap: 20px;
    align-items: stretch;
  }

  .professional_projects_title {
    font-size: 28px;
    margin-bottom: 30px;
  }

  .professional_project_content {
    padding: 20px;
    gap: 12px;
  }

  .professional_project_name {
    font-size: 18px;
  }

  .professional_project_period {
    font-size: 14px;
  }

  .professional_project_role {
    font-size: 14px;
  }

  .professional_project_description {
    font-size: 14px;
    line-height: 1.5;
  }

  .professional_project_tech_label {
    font-size: 12px;
  }

  .professional_project_tech_pill {
    padding: 6px 10px;
    font-size: 11px;
  }

  .professional_project_image_container {
    height: 180px;
  }

  .professional_project_card {
    min-height: 400px;
  }
}

@media screen and (max-width: 480px) {
  .professional_projects_section {
    padding: 70px 0;
  }

  .professional_projects_container {
    padding: 0 24px;
  }

  .professional_projects_title {
    font-size: 24px;
    margin-bottom: 25px;
  }

  .professional_project_content {
    padding: 15px;
    gap: 10px;
  }

  .professional_project_name {
    font-size: 16px;
  }

  .professional_project_period {
    font-size: 12px;
  }

  .professional_project_role {
    font-size: 12px;
  }

  .professional_project_description {
    font-size: 13px;
    line-height: 1.4;
  }

  .professional_project_tech_label {
    font-size: 11px;
  }

  .professional_project_tech_pill {
    padding: 5px 8px;
    font-size: 10px;
  }

  .professional_project_image_container {
    height: 160px;
  }

  .professional_project_card {
    min-height: 350px;
  }

  .professional_project_buttons {
    flex-direction: column;
    gap: 8px;
  }

  .professional_project_button {
    text-align: center;
    padding: 12px 16px;
    font-size: 13px;
  }
}