/* RESUME 토글 스타일 */
.resume_toggle_container {
  position: fixed;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  z-index: 1000;
  transition: all 0.3s ease;
}

.resume_toggle_label {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  letter-spacing: 0.5px;
  transition: color 0.3s ease;
}

.resume_dots_container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.resume_dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background-color: #cccccc;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.resume_dot:hover {
  background-color: #999999;
  transform: scale(1.2);
}

.resume_dot.active {
  background-color: var(--active-color, #00d4aa);
  box-shadow: 0 0 8px var(--active-color-shadow, rgba(0, 212, 170, 0.6));
  transform: scale(1.3);
}

/* 다크모드 스타일 */

[data-theme="dark"] .resume_toggle_label {
  color: #ffffff;
}

[data-theme="dark"] .resume_dot {
  background-color: #666666;
}

[data-theme="dark"] .resume_dot:hover {
  background-color: #cccccc;
}

[data-theme="dark"] .resume_dot.active {
  background-color: var(--active-color, #00d4aa);
  box-shadow: 0 0 8px var(--active-color-shadow, rgba(0, 212, 170, 0.8));
}


/* 태블릿 환경 */

@media (max-width: 1024px) {
  .resume_toggle_container {
    right: 15px;
  }
}

/* 모바일 반응형 - 모든 모바일에서 숨기기 */
@media (max-width: 768px) {
  .resume_toggle_container {
    display: none; /* 모바일에서는 완전히 숨김 */
  }
}
