/* 메인 섹션 스타일 */
.main_section {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: #ffffff;
  overflow: visible;
  padding-top: 120px;
  transition: background-color 0.3s ease;
}

[data-theme="dark"] .main_section {
  background: #1a1a1a;
}

.main_container {
  width: 100%;
  margin: 0 auto;
  z-index: 10;
  text-align: left;
  max-width: 1920px;
  padding: 0 50px;
  position: relative;
}

.main_title {
  font-size: 100px;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 30px;
  letter-spacing: -0.02em;
  line-height: 1.1;
  transition: color 0.3s ease;
}

[data-theme="dark"] .main_title {
  color: #ffffff;
}

.main_description {
  font-size: 19px;
  color: #34495e;
  line-height: 1.8;
  margin: 0;
  font-weight: 400;
  transition: color 0.3s ease;
}

[data-theme="dark"] .main_description {
  color: #cccccc;
}

/* About Me 섹션 */
.about_section {
  position: relative;
  min-height: 100vh;
  background: #ffffff;
  overflow: visible;
  padding-top: 80px;
  transition: background-color 0.3s ease;
}

[data-theme="dark"] .about_section {
  background: #1a1a1a;
}

/* 배경 블러 효과 */
.blur {
  position: absolute;
  border-radius: 50%;
  filter: blur(40px);
  opacity: 0.7;
  animation: float 6s ease-in-out infinite;
  z-index: 1;
}

.blur_1 {
  width: 1500px;
  height: 1500px;
  background: linear-gradient(45deg, #ffb3b3, #ffc4c4);
  top: -20%;
  left: -10%;
  animation-delay: 0s;
  transform: translate3d(-239px, 198px, 0px) rotate(24deg);
}

.blur_2 {
  width: 500px;
  height: 500px;
  background: linear-gradient(45deg, #a8e6cf, #b8f2d8);
  top: 30%;
  right: -5%;
  animation-delay: 2s;
  transform: translate3d(-131px, 66px, 0px) rotate(-2deg);
}

.blur_3 {
  width: 750px;
  height: 750px;
  background: linear-gradient(45deg, #c8e6ff, #d4f0ff);
  bottom: -2%;
  left: 29%;
  animation-delay: 4s;
  transform: translate3d(159px, 180px, 0px) rotate(-38deg);
}

.blur_4 {
  width: 800px;
  height: 800px;
  background: linear-gradient(45deg, #fff8e1, #fff3b0);
  top: 40%;
  left: 0%;
  animation-delay: 1s;
  transform: translate3d(100px, -50px, 0px) rotate(15deg);
}

[data-theme="dark"] .blur {
  opacity: 0.3;
  filter: blur(60px);
}

[data-theme="dark"] .blur_1 {
  background: linear-gradient(
    45deg,
    rgba(255, 179, 179, 0.4),
    rgba(255, 196, 196, 0.4)
  );
}

[data-theme="dark"] .blur_2 {
  background: linear-gradient(
    45deg,
    rgba(168, 230, 207, 0.4),
    rgba(184, 242, 216, 0.4)
  );
}

[data-theme="dark"] .blur_3 {
  background: linear-gradient(
    45deg,
    rgba(200, 230, 255, 0.4),
    rgba(212, 240, 255, 0.4)
  );
}

[data-theme="dark"] .blur_4 {
  background: linear-gradient(
    45deg,
    rgba(255, 248, 225, 0.4),
    rgba(255, 243, 176, 0.4)
  );
}

@keyframes float {
  0%,
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  33% {
    transform: translate3d(30px, -30px, 0) rotate(120deg);
  }
  66% {
    transform: translate3d(-20px, 20px, 0) rotate(240deg);
  }
}

.about_container {
  width: 100%;
  margin: 0 auto;
  z-index: 10;
  text-align: left;
  max-width: 1900px;
  position: relative;
}

.section_title {
  font-size: 100px;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 30px;
  letter-spacing: -0.02em;
  line-height: 1.1;
  transition: color 0.3s ease;
}

[data-theme="dark"] .section_title {
  color: #ffffff;
}

.about_description {
  font-size: 19px;
  color: #34495e;
  line-height: 1.8;
  margin: 0;
  font-weight: 400;
  transition: color 0.3s ease;
}

[data-theme="dark"] .about_description {
  color: #cccccc;
}

/* 반응형 디자인 */
@media (max-width: 1024px) {
  .main_container {
    padding: 0 50px;
  }
  
  .main_title {
    font-size: 80px;
  }
  
  .main_description {
    font-size: 18px;
  }
}

@media (max-width: 768px) {
  .main_section {
    padding-top: 80px;
  }

  .main_title {
    font-size: 48px;
    line-height: 1.2;
    margin-bottom: 25px;
  }

  .main_description {
    font-size: 16px;
    line-height: 1.6;
    word-break: keep-all;
  }

  .main_container {
    padding: 0 15px;
    margin-left: 0;
  }

  .blur_1 {
    width: 800px;
    height: 800px;
    top: -30%;
    left: -15%;
  }

  .blur_2 {
    width: 400px;
    height: 400px;
    top: 20%;
    right: -10%;
  }

  .blur_3 {
    width: 500px;
    height: 500px;
    bottom: -15%;
    left: 10%;
  }

  .blur_4 {
    width: 600px;
    height: 600px;
    top: 30%;
    left: -5%;
  }
}

@media (max-width: 480px) {
  .main_section {
    padding-top: 70px;
  }

  .main_title {
    font-size: 40px;
    line-height: 1.1;
    margin-bottom: 20px;
    text-align: center;
  }

  .main_description {
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
    word-break: keep-all;
  }

  .main_container {
    padding: 0 24px;
    margin-left: 0;
  }

  .blur_1 {
    width: 600px;
    height: 600px;
    top: -25%;
    left: -20%;
  }

  .blur_2 {
    width: 300px;
    height: 300px;
    top: 15%;
    right: -15%;
  }

  .blur_3 {
    width: 400px;
    height: 400px;
    bottom: -20%;
    left: 5%;
  }

  .blur_4 {
    width: 500px;
    height: 500px;
    top: 25%;
    left: -10%;
  }
}

@media (max-width: 360px) {
  .main_container {
    padding: 0 8px;
  }

  .main_title {
    font-size: 32px;
    line-height: 1.1;
  }

  .main_description {
    font-size: 13px;
    line-height: 1.4;
  }
}