/* 프로젝트 섹션 스타일 */
.work_section {
  width: 100vw;
  height: 100vh;
  padding: 300px 0;
  min-height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(-50vw + 50%);
  position: relative;
  z-index: 1;
}

.work_container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0;
}

.work_title {
  margin: 0 auto;
  font-size: 48px;
  font-weight: 700;
  color: #333333;
  text-align: left;
  margin-bottom: 50px;
  padding: 0 40px;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  position: relative;
  letter-spacing: -0.02em;
  transition: color 0.3s ease;
}

[data-theme="dark"] .work_title {
  color: #ffffff;
}

.work_grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  perspective: 1200px;
  padding: 0 40px;
}

.work_card {
  background: #f8f9fa;
  border-radius: 20px;
  padding: 35px 25px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.05);
  cursor: pointer;
  transform-style: preserve-3d;
  will-change: transform;
  min-height: 550px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.work_card:hover {
  background: #f8f9ff !important;
  color: #4a5568;
  border: 1px solid rgba(139, 92, 246, 0.2);
}

.work_card:hover .work_cursor_icon {
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.8);
}

.work_card:hover .work_cursor_text {
  opacity: 0;
}

[data-theme="dark"] .work_card {
  background: #2d2d2d;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

[data-theme="dark"] .work_card:hover {
  background: #2a2a2a !important;
  color: #ffffff;
  border: 1px solid rgba(139, 156, 255, 0.3);
}

.work_card:nth-child(1) {
  background: #f8f9fa;
}

.work_card:nth-child(2) {
  background: #f8f9fa;
}

.work_card:nth-child(3) {
  background: #f8f9fa;
  min-height: 700px;
}

[data-theme="dark"] .work_card:nth-child(1),
[data-theme="dark"] .work_card:nth-child(2),
[data-theme="dark"] .work_card:nth-child(3) {
  background: #2d2d2d !important;
}

[data-theme="dark"] .work_card:nth-child(3) {
  min-height: 700px;
}

.work_card_title {
  font-size: 28px;
  font-weight: 700;
  color: inherit;
  margin-bottom: 30px;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  position: relative;
  z-index: 3;
  line-height: 1.3;
  letter-spacing: -0.01em;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.work_card_number {
  font-size: 48px;
  font-weight: 800;
  color: #553c9a;
  line-height: 1;
  letter-spacing: -0.02em;
  transition: color 0.3s ease;
}

[data-theme="dark"] .work_card_number {
  color: #8b9cff;
}

.work_card_text {
  font-size: 20px;
  font-weight: 600;
  color: inherit;
  line-height: 1.2;
  letter-spacing: -0.01em;
}

.work_project_list {
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;
  z-index: 3;
  flex-grow: 1;
}

.work_project_item {
  font-size: 16px;
  color: inherit;
  margin-bottom: 16px;
  padding: 8px 0;
  transition: all 0.3s ease;
  position: relative;
  opacity: 0.7;
  font-weight: 400;
  border-bottom: 1px solid transparent;
}

.work_project_item:hover {
  opacity: 1;
  transform: translateX(8px);
  font-weight: 500;
  border-bottom-color: rgba(139, 92, 246, 0.3);
  color: #553c9a;
}

[data-theme="dark"] .work_project_item {
  color: #d0d0d0;
}

[data-theme="dark"] .work_project_item:hover {
  border-bottom-color: rgba(139, 156, 255, 0.4);
  color: #8b9cff;
}

.work_cursor_icon {
  position: absolute;
  bottom: 50px;
  right: 3%;
  transform: translateX(-50%);
  opacity: 0.6;
  transition: all 0.4s ease;
  pointer-events: none;
  z-index: 4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.work_cursor_icon img {
  width: 62px;
  height: 62px;
  object-fit: contain;
  display: block;
}

.work_cursor_text {
  font-size: 12px;
  font-weight: 600;
  color: #666666;
  opacity: 0.6;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-align: center;
  display: block;
  margin: 0 auto;
  white-space: nowrap;
  transition: color 0.3s ease;
}

[data-theme="dark"] .work_cursor_text {
  color: #b0b0b0;
}

/* Swiper 스타일 */
.work_swiper {
  width: calc(100% - 48px);
  height: 100%;
  padding: 0;
  display: none;
  margin: 0 auto;
}

.work_swiper .swiper-slide {
  height: auto;
  display: flex;
  align-items: stretch;
  width: 100%;
}

.work_swiper .swiper-slide .work_card {
  width: 100%;
  margin: 0;
  border: 3px solid rgba(54, 91, 255, 0.1);
  transform: scale(0.95);
  transition: transform 0.3s ease;
  border-radius: 10px;
}

.work_swiper .swiper-pagination {
  bottom: 30px;
  display: flex;
  justify-content: center;
  gap: 8px;
}

.work_swiper .swiper-pagination-bullet {
  background: #667eea;
  opacity: 0.3;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.work_swiper .swiper-pagination-bullet-active {
  opacity: 1;
  transform: scale(1.2);
  background: #667eea;
}

/* 반응형 디자인 */
@media screen and (max-width: 1200px) {
  .work_grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }

  .work_card {
    padding: 40px 30px;
    min-height: 650px;
  }

  .work_title {
    font-size: 40px;
  }
}

@media screen and (max-width: 1024px) {
  .work_section {
    padding: 20px 0 150px 0;
    height: 100%;
  }

  .work_container {
    padding: 0 40px;
  }

  .work_grid {
    display: none;
  }

  .work_swiper {
    display: block;
    width: calc(100% - 80px);
  }

  .work_title {
    font-size: 44px;
    margin-bottom: 50px;
  }

  .work_card {
    padding: 35px 30px;
    height: 600px;
    max-height: 600px;
    min-height: 600px;
    box-shadow: 0 12px 35px rgba(0, 0, 0, 0.15);
    margin: 0 10px;
  }

  .work_card_title {
    font-size: 26px;
    margin-bottom: 30px;
  }

  .work_card_number {
    font-size: 44px;
  }

  .work_card_text {
    font-size: 18px;
  }

  .work_project_list {
    gap: 15px;
    margin-bottom: 40px;
  }

  .work_project_item {
    font-size: 17px;
    padding: 10px 0;
    margin-bottom: 18px;
  }

  .work_cursor_icon {
    bottom: 35px;
    right: 30px;
  }

  .work_cursor_icon img {
    width: 55px;
    height: 55px;
  }

  .work_cursor_text {
    font-size: 13px;
  }

  .work_swiper .swiper-pagination {
    bottom: 40px;
  }

  .work_swiper .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    margin: 0 6px;
  }

  .swiper {
    height: 750px;
  }
}

@media screen and (max-width: 768px) {
  .work_section {
    padding: 10px 0px 30px 0px;
  }

  .work_container {
    padding: 0 15px;
  }

  .work_grid {
    display: none;
  }

  .work_swiper {
    display: block;
  }

  .work_card {
    padding: 15px 24px;
    height: 500px;
    max-height: 500px;
    min-height: 500px;
    box-shadow: none;
  }

  .work_title {
    font-size: 36px;
    margin-bottom: 30px;
  }

  .work_card_title {
    font-size: 20px;
    margin-bottom: 30px;
  }

  .work_card_number {
    font-size: 36px;
  }

  .work_card_text {
    font-size: 16px;
  }

  .work_project_item {
    font-size: 16px;
    margin-bottom: 15px;
  }

  .work_cursor_icon {
    bottom: 35px;
    transform: translateX(-50%);
  }

  .work_cursor_icon img {
    width: 50px;
    height: 50px;
  }

  .work_cursor_text {
    font-size: 10px;
  }

  .swiper {
    height: 580px;
  }
}

@media screen and (max-width: 480px) {
  .work_title {
    font-size: 32px;
  }

  .work_card {
    padding: 25px 15px;
  }

  .work_project_item {
    font-size: 13px;
  }

  .work_card_number {
    font-size: 28px;
  }

  .work_card_text {
    font-size: 14px;
  }

  .work_cursor_text {
    font-size: 9px;
  }
}