/* 웹 디자인 섹션 스타일 */
.web_design_section {
  background-color: #f8fafc;
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.web_design_container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 150px 20px;
}

.web_design_title {
  color: #1e293b;
  font-size: 50px;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
  text-align: center;
  margin-bottom: 80px;
  letter-spacing: -0.02em;
}

.web_design_grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  perspective: 1200px;
}

.web_design_card {
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: pointer;
  position: relative;
  transform-style: preserve-3d;
}

.web_design_card:hover {
  transform: translateY(-15px) rotateX(5deg) rotateY(5deg);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.web_design_card_image_container {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
}

.web_design_card_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.web_design_card:hover .web_design_card_image {
  transform: scale(1.1);
}

.web_design_card_overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(255, 182, 193, 0.95) 0%,
    rgba(221, 160, 221, 0.95) 50%,
    rgba(175, 238, 238, 0.95) 100%
  );
  opacity: 0;
  transition: all 0.4s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.web_design_card:hover .web_design_card_overlay {
  opacity: 1;
}

.web_design_card_info {
  text-align: center;
  color: white;
  transform: translateY(20px);
  transition: transform 0.4s ease;
}

.web_design_card:hover .web_design_card_info {
  transform: translateY(0);
}

.web_design_click_hint {
  position: absolute;
  bottom: -25px;
  left: 50%;
  transform: translateX(-50%) translateY(10px);
  background: rgba(255, 255, 255, 0.9);
  color: #333;
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 1px;
  opacity: 0;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.web_design_card:hover .web_design_click_hint {
  opacity: 1;
  transform: translateX(-50%) translateY(0);
}

.web_design_card_title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 15px;
  color: white;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.web_design_card_description {
  font-size: 14px;
  line-height: 1.6;
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.9);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.web_design_card_tech_stack {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
  margin-bottom: 20px;
}

.web_design_tech_pill {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.web_design_card_meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.8);
}

.web_design_card_year {
  font-weight: 600;
}

.web_design_card_category {
  background: rgba(255, 255, 255, 0.2);
  padding: 4px 8px;
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.web_design_card_link {
  display: inline-block;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 12px 24px;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.web_design_card_link:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* 모달 스타일 */
.web_design_modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(10px);
  animation: modalFadeIn 0.3s ease-out;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.web_design_modal_content {
  background: white;
  border-radius: 20px;
  max-width: 90vw;
  max-height: 90vh;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  animation: modalSlideIn 0.3s ease-out;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
}

@keyframes modalSlideIn {
  from {
    transform: scale(0.8) translateY(50px);
    opacity: 0;
  }
  to {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

.web_design_modal_close {
  position: absolute;
  top: 20px;
  right: 20px;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 24px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  line-height: 1;
  padding: 0;
  margin: 0;
}

.web_design_modal_close:hover {
  background: rgba(0, 0, 0, 0.8);
  transform: scale(1.1);
}

.web_design_modal_image_container {
  width: 100%;
  height: 60vh;
  overflow: hidden;
  position: relative;
}

.web_design_modal_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.web_design_modal_image:hover {
  transform: scale(1.05);
}

.web_design_modal_info {
  padding: 30px;
  background: linear-gradient(
    135deg,
    rgba(255, 182, 193, 0.1) 0%,
    rgba(221, 160, 221, 0.1) 50%,
    rgba(175, 238, 238, 0.1) 100%
  );
}

.web_design_modal_title {
  font-size: 28px;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 15px;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.web_design_modal_description {
  font-size: 16px;
  line-height: 1.6;
  color: #64748b;
  margin-bottom: 25px;
}

.web_design_modal_tech_stack {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 25px;
}

.web_design_modal_tech_pill {
  background: linear-gradient(
    135deg,
    rgba(255, 182, 193, 0.2) 0%,
    rgba(221, 160, 221, 0.2) 50%,
    rgba(175, 238, 238, 0.2) 100%
  );
  color: #1e293b;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
  border: 1px solid rgba(255, 182, 193, 0.3);
}

.web_design_modal_meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  font-size: 14px;
  color: #64748b;
}

.web_design_modal_year {
  font-weight: 600;
  color: #1e293b;
}

.web_design_modal_category {
  background: linear-gradient(
    135deg,
    rgba(255, 182, 193, 0.2) 0%,
    rgba(221, 160, 221, 0.2) 50%,
    rgba(175, 238, 238, 0.2) 100%
  );
  color: #1e293b;
  padding: 6px 12px;
  border-radius: 15px;
  font-weight: 500;
  border: 1px solid rgba(255, 182, 193, 0.3);
}

/* 애니메이션 효과 */
@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.web_design_card:nth-child(even) {
  animation: float 6s ease-in-out infinite;
  animation-delay: 0.5s;
}

.web_design_card:nth-child(odd) {
  animation: float 6s ease-in-out infinite;
  animation-delay: 1s;
}

.web_design_card {
  opacity: 0;
  transform: translateY(60px) scale(0.8);
}

/* 반응형 디자인 */
@media screen and (max-width: 1024px) {
  .web_design_container {
    padding: 120px 30px;
  }

  .web_design_title {
    font-size: 42px;
    margin-bottom: 60px;
  }

  .web_design_grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 35px;
  }

  .web_design_card_image_container {
    height: 220px;
  }

  .web_design_card_title {
    font-size: 22px;
  }

  .web_design_card_description {
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  .web_design_container {
    padding: 100px 20px;
  }

  .web_design_title {
    font-size: 36px;
    margin-bottom: 50px;
  }

  .web_design_grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .web_design_card_image_container {
    height: 200px;
  }

  .web_design_card_overlay {
    padding: 20px;
  }

  .web_design_card_title {
    font-size: 20px;
    margin-bottom: 12px;
  }

  .web_design_card_description {
    font-size: 13px;
    margin-bottom: 18px;
  }

  .web_design_tech_pill {
    font-size: 12px;
    padding: 6px 10px;
  }

  .web_design_card_link {
    padding: 12px 24px;
    font-size: 14px;
  }

  .web_design_click_hint {
    font-size: 10px;
    padding: 5px 10px;
  }
}

@media screen and (max-width: 480px) {
  .web_design_container {
    padding: 80px 15px;
  }

  .web_design_title {
    font-size: 28px;
    margin-bottom: 40px;
  }

  .web_design_grid {
    gap: 25px;
  }

  .web_design_card_image_container {
    height: 180px;
  }

  .web_design_card_overlay {
    padding: 15px;
  }

  .web_design_card_title {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .web_design_card_description {
    font-size: 12px;
    margin-bottom: 15px;
  }

  .web_design_tech_pill {
    font-size: 11px;
    padding: 5px 8px;
  }

  .web_design_card_link {
    padding: 10px 20px;
    font-size: 13px;
  }

  .web_design_click_hint {
    font-size: 9px;
    padding: 4px 8px;
  }
}

/* 모달 반응형 */
@media screen and (max-width: 1024px) {
  .web_design_modal_content {
    max-width: 90vw;
    max-height: 90vh;
    margin: 30px;
  }

  .web_design_modal_image_container {
    height: 50vh;
  }

  .web_design_modal_info {
    padding: 25px;
  }

  .web_design_modal_title {
    font-size: 26px;
