    font-size: 18px;
  }

  .web_design_modal_meta {
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
  }
}

/* 다크모드 스타일 */
[data-theme="dark"] .web_design_section {
  background-color: #0a0a0a;
}

[data-theme="dark"] .web_design_title {
  color: #ffffff;
}

[data-theme="dark"] .web_design_card {
  background: #1a1a1a;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

[data-theme="dark"] .web_design_card:hover {
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.7);
}

[data-theme="dark"] .web_design_card_overlay {
  backdrop-filter: blur(15px);
  background: rgba(0, 0, 0, 0.3);
}

[data-theme="dark"] .web_design_modal_content {
  background: #1a1a1a;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

[data-theme="dark"] .web_design_modal_info {
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.05) 0%,
    rgba(255, 255, 255, 0.03) 50%,
    rgba(255, 255, 255, 0.05) 100%
  );
}

[data-theme="dark"] .web_design_modal_title {
  color: #ffffff;
}

[data-theme="dark"] .web_design_modal_description {
  color: #b0b0b0;
}

[data-theme="dark"] .web_design_modal_tech_pill {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

[data-theme="dark"] .web_design_modal_meta {
  color: #b0b0b0;
}

[data-theme="dark"] .web_design_modal_year {
  color: #ffffff;
}

[data-theme="dark"] .web_design_modal_category {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.2);
}