/* Contact 섹션 스타일 */
.contact_section {
  position: relative;
  min-height: 100vh;
  background: transparent;
  overflow: hidden;
  padding: 0;
  transition: background-color 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* 배경 블러 효과 */
.contact_blur_1 {
  position: absolute;
  width: 800px;
  height: 800px;
  background: linear-gradient(45deg, #ffb3b3, #ffc4c4);
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
  animation: contact_float 8s ease-in-out infinite;
  top: -40%;
  left: -30%;
  z-index: 1;
}

.contact_blur_2 {
  position: absolute;
  width: 700px;
  height: 700px;
  background: linear-gradient(45deg, #a8e6cf, #b8f2d8);
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
  animation: contact_float 6s ease-in-out infinite reverse;
  top: -20%;
  right: -35%;
  z-index: 1;
}

.contact_blur_3 {
  position: absolute;
  width: 750px;
  height: 750px;
  background: linear-gradient(45deg, #c8e6ff, #d4f0ff);
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
  animation: contact_float 10s ease-in-out infinite;
  bottom: -50%;
  left: -20%;
  z-index: 1;
}

.contact_blur_4 {
  position: absolute;
  width: 650px;
  height: 650px;
  background: linear-gradient(45deg, #fff8e1, #fff3b0);
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
  animation: contact_float 7s ease-in-out infinite reverse;
  top: 20%;
  right: -25%;
  z-index: 1;
}

@keyframes contact_float {
  0%,
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  33% {
    transform: translate3d(30px, -30px, 0) rotate(120deg);
  }
  66% {
    transform: translate3d(-20px, 20px, 0) rotate(240deg);
  }
}

.contact_container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 50px 50px;
  position: relative;
  z-index: 10;
}

.contact_content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 80px;
  width: 100%;
}

/* 연락처 정보 영역 */
.contact_info {
  display: flex;
  flex-direction: column;
  gap: 40px;
  align-items: center;
  text-align: center;
}

.contact_title {
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: center;
}

.contact_sparkle {
  font-size: 32px;
  color: #667eea;
  animation: sparkle_glow 2s ease-in-out infinite alternate;
}

@keyframes sparkle_glow {
  0% {
    transform: scale(1) rotate(0deg);
  }
  100% {
    transform: scale(1.1) rotate(10deg);
  }
}

.contact_title h2 {
  font-size: 64px;
  font-weight: 700;
  color: #2c3e50;
  margin: 0;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  letter-spacing: -0.02em;
  line-height: 1.1;
  transition: color 0.3s ease;
}

[data-theme="dark"] .contact_title h2 {
  color: #ffffff;
}

.contact_subtitle {
  font-size: 19px;
  color: #667eea;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 0;
  transition: color 0.3s ease;
}

[data-theme="dark"] .contact_subtitle {
  color: #a8e6cf;
}

.contact_details {
  display: flex;
  flex-direction: column;
  gap: 25px;
  align-items: center;
}

.contact_item {
  display: flex;
  align-items: center;
  gap: 20px;
  color: #34495e;
  font-size: 21px;
  font-weight: 500;
  transition: color 0.3s ease;
  justify-content: center;
}

[data-theme="dark"] .contact_item {
  color: #cccccc;
}

.contact_icon {
  font-size: 29px;
  width: 40px;
  text-align: center;
}

.contact_social {
  display: flex;
  gap: 20px;
  margin-top: 15px;
  align-items: center;
  justify-content: center;
}

/* 메일보내기 섹션 */
.contact_mail_section {
  margin-top: 30px;
}

.contact_mail_btn {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  background: #2c3e50;
  color: white;
  padding: 16px 32px;
  border-radius: 50px;
  text-decoration: none;
  font-weight: 600;
  font-size: 18px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(44, 62, 80, 0.3);
}

.contact_mail_btn:hover {
  background: #34495e;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(44, 62, 80, 0.4);
}

.mail_btn_icon {
  width: 32px;
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.contact_mail_btn:hover .mail_btn_icon {
  background: rgba(255, 255, 255, 0.3);
  transform: translateX(3px);
}

.contact_social_link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: transparent;
  border: none;
  transition: all 0.3s ease;
  text-decoration: none;
}

.contact_social_link:hover {
  background: transparent;
  border: none;
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(44, 62, 80, 0.3);
}

.contact_social_icon {
  font-size: 40px;
  color: #2c3e50;
  width: 40px;
  height: 40px;
}

/* QR 코드 영역 */
.contact_qr_section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact_qr_container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact_qr_code {
  width: 250px;
  height: 250px;
  background: #ffffff;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.contact_qr_code:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.contact_qr_link {
  display: block;
  text-decoration: none;
  transition: all 0.3s ease;
}

.contact_qr_link:hover {
  transform: translateY(-5px);
}

.contact_qr_image {
  width: 220px;
  height: 220px;
  object-fit: cover;
  border-radius: 8px;
  position: relative;
  transition: all 0.3s ease;
}

.contact_qr_link:hover .contact_qr_image {
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
}

/* 감사 인사 */
.contact_thank_you {
  text-align: center;
  margin-top: 30px;
  padding: 0;
}

.contact_thank_you p {
  font-size: 29px;
  color: #2c3e50;
  font-weight: 600;
  margin: 0;
  transition: color 0.3s ease;
}

.contact_thank_you span {
  font-size: 32px;
  margin-left: 10px;
}

[data-theme="dark"] .contact_thank_you p {
  color: #ffffff;
}

[data-theme="dark"] .contact_mail_btn {
  background: #ffffff;
  color: #2c3e50;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.3);
}

[data-theme="dark"] .contact_mail_btn:hover {
  background: #f0f0f0;
  box-shadow: 0 6px 20px rgba(255, 255, 255, 0.4);
}

[data-theme="dark"] .contact_social_icon {
  color: #ffffff;
}

/* 이메일 폼 섹션 */
.contact_form_section {
  width: 100%;
  padding: 80px 0;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
  position: relative;
  z-index: 10;
}

[data-theme="dark"] .contact_form_section {
  background: rgba(26, 26, 46, 0.8);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

/* 다크모드 블러 효과 */
[data-theme="dark"] .contact_blur_1,
[data-theme="dark"] .contact_blur_2,
[data-theme="dark"] .contact_blur_3,
[data-theme="dark"] .contact_blur_4 {
  opacity: 0.2;
  filter: blur(80px);
}

[data-theme="dark"] .contact_blur_1 {
  background: linear-gradient(
    45deg,
    rgba(255, 179, 179, 0.4),
    rgba(255, 196, 196, 0.4)
  );
}

[data-theme="dark"] .contact_blur_2 {
  background: linear-gradient(
    45deg,
    rgba(168, 230, 207, 0.4),
    rgba(184, 242, 216, 0.4)
  );
}

[data-theme="dark"] .contact_blur_3 {
  background: linear-gradient(
    45deg,
    rgba(200, 230, 255, 0.4),
    rgba(212, 240, 255, 0.4)
  );
}

[data-theme="dark"] .contact_blur_4 {
  background: linear-gradient(
    45deg,
    rgba(255, 248, 225, 0.4),
    rgba(255, 243, 176, 0.4)
  );
}

.contact_form_container {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 50px;
  position: relative;
  z-index: 11;
}

.contact_form_header {
  text-align: center;
  margin-bottom: 50px;
}

.contact_form_header h3 {
  font-size: 40px;
  font-weight: 700;
  color: #2c3e50;
  margin: 0 0 15px 0;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  transition: color 0.3s ease;
}

[data-theme="dark"] .contact_form_header h3 {
  color: #ffffff;
}

.contact_form_header p {
  font-size: 18px;
  color: #667eea;
  margin: 0;
  transition: color 0.3s ease;
}

[data-theme="dark"] .contact_form_header p {
  color: #a8e6cf;
}

.contact_form {
  display: flex;
  flex-direction: column;
  gap: 25px;
  position: relative;
  z-index: 12;
}

.contact_form_group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.contact_form_group label {
  font-size: 16px;
  font-weight: 600;
  color: #2c3e50;
  transition: color 0.3s ease;
}

[data-theme="dark"] .contact_form_group label {
  color: #ffffff;
}

.contact_form_group input,
.contact_form_group textarea {
  padding: 15px 20px;
  border: 2px solid rgba(102, 126, 234, 0.2);
  border-radius: 12px;
  font-size: 16px;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background: rgba(255, 255, 255, 0.8);
  color: #2c3e50;
  transition: all 0.3s ease;
  resize: vertical;
  position: relative;
  z-index: 13;
}

.contact_form_group input:focus,
.contact_form_group textarea:focus {
  outline: none;
  border-color: #667eea;
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

[data-theme="dark"] .contact_form_group input,
[data-theme="dark"] .contact_form_group textarea {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border-color: rgba(255, 255, 255, 0.2);
}

[data-theme="dark"] .contact_form_group input:focus,
[data-theme="dark"] .contact_form_group textarea:focus {
  background: rgba(255, 255, 255, 0.15);
  border-color: #667eea;
}

.contact_form_group input::placeholder,
.contact_form_group textarea::placeholder {
  color: rgba(44, 62, 80, 0.6);
  transition: color 0.3s ease;
}

[data-theme="dark"] .contact_form_group input::placeholder,
[data-theme="dark"] .contact_form_group textarea::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.contact_form_submit {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #ffffff;
  border: none;
  padding: 18px 40px;
  border-radius: 12px;
  font-size: 18px;
  font-weight: 700;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  z-index: 14;
}

.contact_form_submit:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.contact_form_submit:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.contact_form_submit.submitting {
  background: linear-gradient(135deg, #a8e6cf 0%, #88d8a3 100%);
}

.contact_form_message {
  padding: 15px 20px;
  border-radius: 12px;
  font-weight: 600;
  text-align: center;
  margin-top: 10px;
  animation: slideIn 0.3s ease;
}

.contact_form_message.success {
  background: rgba(168, 230, 207, 0.2);
  color: #2d5a3d;
  border: 1px solid rgba(168, 230, 207, 0.5);
}

.contact_form_message.error {
  background: rgba(255, 182, 193, 0.2);
  color: #8b3a3a;
  border: 1px solid rgba(255, 182, 193, 0.5);
}

[data-theme="dark"] .contact_form_message.success {
  background: rgba(168, 230, 207, 0.15);
  color: #a8e6cf;
  border-color: rgba(168, 230, 207, 0.3);
}

[data-theme="dark"] .contact_form_message.error {
  background: rgba(255, 182, 193, 0.15);
  color: #ffb6c1;
  border-color: rgba(255, 182, 193, 0.3);
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 저작권 섹션 */
.contact_copyright_section {
  background: transparent;
  padding: 20px 0;
  text-align: center;
  border-top: none;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.contact_copyright_text {
  color: #888888;
  font-size: 14px;
  margin: 0;
  font-weight: 400;
  text-align: center;
}

/* 반응형 디자인 */
@media (max-width: 1200px) {
  .contact_container {
    padding: 40px 40px;
  }

  .contact_content {
    gap: 60px;
  }

  .contact_title h2 {
    font-size: 56px;
  }

  .contact_qr_code {
    width: 220px;
    height: 220px;
  }

  .contact_qr_image {
    width: 190px;
    height: 190px;
  }
}

@media (max-width: 1024px) {
  .contact_container {
    padding: 35px 50px;
  }

  .contact_content {
    gap: 50px;
  }

  .contact_title h2 {
    font-size: 51px;
  }

  .contact_subtitle {
    font-size: 18px;
  }

  .contact_item {
    font-size: 20px;
  }

  .contact_qr_code {
    width: 200px;
    height: 200px;
  }

  .contact_qr_image {
    width: 170px;
    height: 170px;
  }

  .contact_info {
    align-items: flex-start;
  }

  .contact_details {
    align-items: flex-start;
  }
}

@media (max-width: 768px) {
  .contact_container {
    padding: 30px 30px;
  }

  .contact_copyright_section {
    padding-left: 0;
  }

  .contact_content {
    flex-direction: column;
    gap: 50px;
    text-align: center;
    align-items: center;
  }

  .contact_info {
    align-items: center;
  }

  .contact_title {
    justify-content: center;
  }

  .contact_title h2 {
    font-size: 45px;
  }

  .contact_subtitle {
    font-size: 18px;
  }

  .contact_item {
    font-size: 19px;
    justify-content: center;
  }

  .contact_social {
    justify-content: center;
  }

  .contact_qr_code {
    width: 200px;
    height: 200px;
  }

  .contact_qr_image {
    width: 170px;
    height: 170px;
  }

  .contact_thank_you {
    margin-top: 20px;
    text-align: center;
  }

  .contact_thank_you p {
    font-size: 24px;
  }

  .contact_blur_1 {
    width: 500px;
    height: 500px;
    top: -25%;
    left: -20%;
  }

  .contact_blur_2 {
    width: 450px;
    height: 450px;
    top: 10%;
    right: -25%;
  }

  .contact_blur_3 {
    width: 480px;
    height: 480px;
    bottom: -20%;
    left: -15%;
  }

  .contact_blur_4 {
    width: 420px;
    height: 420px;
    top: 30%;
    right: -20%;
  }
}

@media (max-width: 480px) {
  .contact_container {
    padding: 20px 20px;
  }

  .contact_copyright_section {
    padding-left: 0;
  }

  .contact_content {
    gap: 40px;
  }

  .contact_title h2 {
    font-size: 35px;
  }

  .contact_subtitle {
    font-size: 16px;
  }

  .contact_item {
    font-size: 18px;
    gap: 15px;
  }

  .contact_icon {
    font-size: 24px;
    width: 35px;
  }

  .contact_social {
    gap: 15px;
  }

  .contact_social_link {
    width: 50px;
    height: 50px;
  }

  .contact_social_icon {
    width: 35px;
    height: 35px;
  }

  .contact_mail_btn {
    padding: 14px 28px;
    font-size: 16px;
  }

  .contact_qr_code {
    width: 180px;
    height: 180px;
  }

  .contact_qr_image {
    width: 150px;
    height: 150px;
  }

  .contact_thank_you p {
    font-size: 21px;
  }

  .contact_blur_1 {
    width: 350px;
    height: 350px;
    top: -20%;
    left: -25%;
  }

  .contact_blur_2 {
    width: 300px;
    height: 300px;
    top: 5%;
    right: -30%;
  }

  .contact_blur_3 {
    width: 320px;
    height: 320px;
    bottom: -15%;
    left: -20%;
  }

  .contact_blur_4 {
    width: 280px;
    height: 280px;
    top: 25%;
    right: -25%;
  }

  /* 이메일 폼 반응형 */
  .contact_form_container {
    padding: 0 20px;
  }

  .contact_form_header h3 {
    font-size: 32px;
  }

  .contact_form_header p {
    font-size: 16px;
  }

  .contact_form_submit {
    padding: 15px 30px;
    font-size: 16px;
  }
}

@media (max-width: 360px) {
  .contact_container {
    padding: 15px 15px;
  }

  .contact_copyright_section {
    padding-left: 0;
  }

  .contact_title h2 {
    font-size: 29px;
  }

  .contact_subtitle {
    font-size: 14px;
  }

  .contact_item {
    font-size: 16px;
    gap: 12px;
  }

  .contact_icon {
    font-size: 21px;
    width: 30px;
  }

  .contact_social_link {
    width: 45px;
    height: 45px;
  }

  .contact_social_icon {
    width: 30px;
    height: 30px;
  }

  .contact_mail_btn {
    padding: 12px 24px;
    font-size: 14px;
  }

  .contact_qr_code {
    width: 160px;
    height: 160px;
  }

  .contact_qr_image {
    width: 130px;
    height: 130px;
  }

  .contact_thank_you p {
    font-size: 18px;
  }
}